@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Sans|Open+Sans|Roboto&display=swap");
.article {
  border-color: #999;
  border-style: solid;
  border-width: 0px;
  box-shadow: 0 0 0.35rem 0.001rem #444;
  margin: 0rem 2rem; }
  .article a {
    text-decoration: none; }

@media only screen and (max-width: 700px) {
  .article {
    margin: 0; } }

.article-topbar {
  background-color: rgba(251, 251, 251, 0.25);
  display: flex;
  font-family: "Open Sans", sans-serif;
  justify-content: space-between;
  margin: 0; }
  .article-topbar a {
    color: white;
    background-color: rgba(0, 0, 0, 0);
    padding: 1rem;
    text-decoration: none;
    transition: 300ms; }
    .article-topbar a:hover {
      background-color: rgba(0, 0, 0, 0.25); }
    .article-topbar a:visited {
      color: white; }

@media only screen and (max-width: 700px) {
  .article-topbar a {
    padding: 0.7rem; } }

.article-topbar-link {
  background-color: #fbfbfb;
  padding: 0.7rem;
  text-align: center; }

.article-content {
  background-color: #fbfbfb;
  font-family: "Open Sans", sans-serif;
  font-size: 1.1rem;
  overflow-x: auto;
  padding: 1rem 3rem 2rem 3rem; }
  .article-content h1 {
    border-bottom: 3px solid black;
    font-size: 3rem;
    font-weight: bold;
    margin: 2rem 0 1rem 0; }
  .article-content h2 {
    font-size: 2.5rem;
    font-weight: bold;
    margin: 2rem 0 0 0; }
  .article-content h3 {
    font-size: 2.25rem;
    font-weight: bold;
    margin: 0.4rem 0 0 0; }
  .article-content h4 {
    font-size: 1.9rem;
    margin: 0; }
  .article-content h5 {
    margin: 0;
    font-size: 1.75rem; }
  .article-content h6 {
    font-size: 1.25rem;
    margin: 0;
    font-weight: normal; }
  .article-content p {
    font-size: 1.1rem; }
  .article-content li {
    font-size: 1.1rem; }

@media only screen and (max-width: 700px) {
  .article-content {
    background-color: #fbfbfb;
    font-size: 1rem;
    padding: 0.3rem 1.1rem 2rem 1.1rem; }
    .article-content h1 {
      border-bottom: 2px solid black;
      font-size: 2rem;
      font-weight: bold; }
    .article-content h2 {
      font-size: 1.9rem;
      font-weight: bold; }
    .article-content h3 {
      font-size: 1.7rem;
      font-weight: bold; }
    .article-content h4 {
      font-size: 1.2rem;
      margin: 0; }
    .article-content h5 {
      font-size: 1.2rem;
      margin: 0; }
    .article-content h6 {
      font-size: 1.1rem;
      margin: 0; }
    .article-content p {
      font-size: 1rem; }
    .article-content ul {
      padding: 0 1rem 0 1.5rem; }
    .article-content li {
      font-size: 1rem; } }

.article-icon {
  color: black;
  display: inline;
  font-size: 28px;
  transition: opacity 300ms; }
  .article-icon:hover {
    color: #0091E7; }

.article-github-icon {
  color: black;
  margin: 0 0.2rem 0.1rem 0.6rem;
  transition: color 300ms; }
  .article-github-icon:hover {
    color: #0091E7; }
